<script>
  const { dialog } = require("@electron/remote");
  import { createSocketConnection, destroySocketConnection } from "./lib/socket";
  import { login, getUserBySessionToken, getSavedTokens } from "./lib/auth";

  import Game from "./Game.svelte";
  import Settings from "./Settings.svelte";
  import Keybinder from "./Keybinder.svelte";
  import PlayerList from "./PlayerList.svelte";
  import Mods from "./Mods.svelte";

  import { sessionToken, systemInfo, onlinePlayers, gameState, playerInfo } from "./stores";

  let tabs = [
    {
      title: "Game",
      component: Game,
      active: true,
    },
    {
      title: "Players Online",
      count: $onlinePlayers.length,
      component: PlayerList,
      scrollable: true,
    },
    // {
    //   title: "Mods",
    //   component: Mods,
    //   scrollable: true,
    //   active: true,
    // },
    // {
    //   title: "Keybinder",
    //   component: Keybinder,
    // },
    {
      title: "Settings",
      component: Settings,
      // active: true,
    },
  ];

  $: {
    tabs[1].count = $onlinePlayers.length;
  }

  const doLogout = async () => {
    if (localStorage.getItem("authToken")) {
      let rememberAccount = await dialog.showMessageBox(null, {
        type: "question",
        buttons: ["Save Character", "Don't Save"],
        title: "Confirmation",
        message: `Keep ${$playerInfo.name} saved on this computer?`,
        detail: "You will be able to login again without entering your password and without 2-factor-auth.",
      });

      // dont save, No button
      if (rememberAccount.response == 1) {
        let savedTokens = getSavedTokens();
        savedTokens = savedTokens.filter((st) => st.id !== $playerInfo.playerId);
        localStorage.setItem("savedTokens", JSON.stringify(savedTokens));
      }

      localStorage.removeItem("authToken");
    }

    destroySocketConnection();
    window.location.reload();
  };
</script>

<div class="flex">
  <div class="flex flex-col h-screen px-4 py-8 bg-dark w-96">
    <div class="flex flex-col justify-between h-full">
      <aside class="flex-1">
        <ul class="space-y-2">
          {#each tabs as tab, index}
            <li>
              <button
                class="flex items-center px-4 py-2 text-white flex-nowrap outline-none hover:bg-white hover:bg-opacity-20 rounded-md w-full transition-opacity"
                class:bg-white={tabs[index].active == true}
                class:bg-opacity-20={tabs[index].active == true}
                on:click|preventDefault={() => {
                  for (const thisTab of tabs) thisTab.active = false;
                  tabs[index].active = true;
                }}
              >
                <span class="mx-4 text-sm font-medium"
                  >{tab.title}

                  {#if tab.count != undefined}
                    <span
                      class="ml-2 py-0.5 px-2.5 rounded-md text-xs font-medium inline-block bg-opacity-90 bg-gray-100 text-verydark"
                      >{tab.count}</span
                    >{/if}
                </span>
              </button>
            </li>
          {/each}
        </ul>
      </aside>
      <div class="justify-self-end px-4">
        <span>{$playerInfo.name.replace("_", " ")}</span>
        <br />
        <span class="text-xs font-semibold text-white opacity-50 hover:opacity-70 cursor-pointer" on:click={doLogout}
          >Logout</span
        >
      </div>
    </div>
  </div>

  <div class="h-screen w-full overflow-hidden">
    <svelte:component this={tabs.find((t) => t.active === true).component} />
  </div>
</div>
