<script>
  import { sessionToken, systemInfo, playerInfo, onlinePlayers, gameState, serverInfo } from "./stores";

  export let onlinePlayerList = [];

  $: {
    onlinePlayerList = $onlinePlayers.map((p) => {
      p.faction = $serverInfo.factions.find((f) => f.idFaction == p.idFaction);
      return p;
    });
  }
</script>

<div class="h-full p-6 flex flex-col overflow-y-scroll">
  <div id="main-area" class="flex-1 space-y-2">
    {#each onlinePlayerList as player}
      <div class="bg-dark text-gray-300 px-6 py-4 rounded-md flex items-center justify-items-center">
        <div class="flex-1">
          <div class="text-gray-200 font-semibold">{player.name.replace("_", " ")}</div>
          <div class="text-xs text-gray-300">
            {#if player.idFaction > 0}
              {player.faction.f_abbr}
            {/if}
          </div>
        </div>

        <div>
          <div>
            {#if player.admin > 0}
              <span class="px-2 inline-flex text-xs leading-5 font-semibold rounded-md bg-red-100 text-red-800">
                Admin
              </span>
            {/if}
            {#if player.helper > 0}
              <span class="px-2 inline-flex text-xs leading-5 font-semibold rounded-md bg-orange-100 text-orange-800">
                Helper
              </span>
            {/if}
            {#if $playerInfo.friendIds.includes(player.id)}<span
                class="px-2 inline-flex text-xs leading-5 font-semibold rounded-md bg-blue-100 text-blue-800"
              >
                Friend
              </span>
            {/if}
          </div>
        </div>
      </div>
    {/each}
  </div>
</div>
