<script>
  const { dialog } = require("@electron/remote");

  import { Icon, LockOpen, ShieldCheck } from "svelte-hero-icons";

  import { login, getUserBySessionToken, getSavedTokens } from "./lib/auth";
  import { createSocketConnection, destroySocketConnection } from "./lib/socket";
  import { onMount } from "svelte";
  import { systemInfo, sessionToken, playerInfo } from "./stores";
  import { getSystemInfo } from "./lib/sysinfo";

  let loginLoading = false;
  let loginFormEl;

  let username = "";
  let password = "";
  let otp = "";
  let rememberMe = false;

  let otpEl;
  let otpRequired = false;

  let myAccounts = getSavedTokens();
  let showLoginForm = !myAccounts || !myAccounts.length;

  $: {
    otp = String(otp)
      .trim()
      .replace(/[^0-9]/gi, "");
  }

  $: {
    const underScorePos = username.indexOf("_");

    username = username.toString();
    if (underScorePos === -1) {
      username = username.replace(" ", "_");
    }
    username = username.replace(/[^a-zA-Z\_]/gi, "");
    username = ucfirst(username);
  }

  function ucfirst(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
  }

  const submitLogin = async (e) => {
    loginLoading = true;

    const loginResult = await login({
      username,
      password,
      otp,
      remember: rememberMe,
      system_uuid: $systemInfo.system.uuid || null,
    });

    if (loginResult.otpRequired) {
      otp = "";
      // otp = loginResult.reqOtp;
      otpRequired = true;
      if (otpEl) otpEl.focus();
      loginLoading = false;
      return;
    }

    if (!loginResult.success) {
      loginLoading = false;
      dialog.showMessageBox({
        message: `Are you sure the username and password you entered are correct?`,
        type: "error",
        title: "Login failed",
      });

      return;
    }

    $playerInfo = await getUserBySessionToken({ token: loginResult.sessionToken });
    $sessionToken = loginResult.sessionToken;
    loginLoading = false;

    createSocketConnection();

    if (loginResult.authToken) {
      let savedTokens = getSavedTokens();
      savedTokens = savedTokens.filter((st) => st.id !== $playerInfo.playerId);

      savedTokens.push({ id: $playerInfo.playerId, name: $playerInfo.name, token: loginResult.authToken });
      localStorage.setItem("savedTokens", JSON.stringify(savedTokens));

      localStorage.setItem("authToken", loginResult.authToken);
    }
  };
</script>

<div class="min-h-screen bg-verydark flex flex-col justify-center py-12 select-none ">
  <!-- <div class="mb-8">
    <img src="./images/logo-white.png" alt="Logo" class="w-96 h-auto mx-auto" />
  </div> -->

  <div class="mx-auto w-2/3 space-y-8">
    {#if myAccounts && myAccounts.length}
      <section class="space-y-4">
        <div>
          <h2 class="text-lg font-semibold tracking-tight text-gray-100">
            <span> Saved Characters</span>
          </h2>
          <p class="text-xs text-gray-400">
            Quickly login to one of your saved characters without entering your password.
          </p>
        </div>

        <div class="space-x-2 text-sm">
          {#each myAccounts as account}
            <div
              class="rounded-md bg-dark hover:bg-opacity-80 text-gray-300 py-2 px-4 inline-flex flex-nowrap items-center cursor-pointer"
              on:click={() => {
                localStorage.setItem("authToken", account.token);
                window.location.reload();
              }}
            >
              <Icon src={LockOpen} class="w-4 h-4 mr-2" />
              <span class="font-semibold"> {account.name.replace("_", " ")}</span>
            </div>
          {/each}
        </div>

        {#if showLoginForm == false}
          <div
            class="cursor-pointer text-imblue-light shadow-sm hover:text-opacity-90 font-semibold text-xs"
            on:click|preventDefault={() => {
              showLoginForm = !showLoginForm;
            }}
          >
            Click here to add another character.
          </div>
        {/if}
      </section>
    {/if}

    {#if showLoginForm == true}
      <section>
        <h2 class="text-lg font-semibold tracking-tight text-gray-100">
          <span>Character Login</span>
        </h2>
        <p class="text-xs text-gray-400">
          Enter your in-game credentials. If this is a public or shared computer, do not save your login info.
        </p>

        <div class="bg-dark p-4 rounded-md mt-4">
          <form
            class="space-y-4"
            method="post"
            on:submit|preventDefault={(e) => submitLogin(e)}
            bind:this={loginFormEl}
          >
            <div class="px-4 flex items-center">
              <label for="username" class="text-gray-300 font-semibold w-[30%] text-sm">Name</label>
              <div class="flex-1 items-center">
                <input
                  bind:value={username}
                  id="username"
                  name="username"
                  type="text"
                  placeholder="Firstname_Lastname"
                  required
                  class="appearance-none block w-full px-3 py-2 text-sm rounded-md placeholder-gray-500 focus:outline-none select-text bg-verydark text-gray-300 border border-dark focus:ring-0 focus:border-gray-600"
                />
              </div>
            </div>

            <div class="px-4 flex items-center">
              <label for="password" class="text-gray-300 font-semibold w-[30%] text-sm">Password</label>
              <div class="flex-1 items-center">
                <input
                  bind:value={password}
                  id="password"
                  name="password"
                  type="password"
                  autocomplete="current-password"
                  placeholder=""
                  required
                  class="appearance-none block w-full px-3 py-2 text-sm rounded-md placeholder-gray-500 focus:outline-none select-text bg-verydark text-gray-300 border border-dark focus:ring-0 focus:border-gray-600"
                />
              </div>
            </div>

            {#if otpRequired === true}
              <div class="px-4 flex items-center">
                <label for="otp" class="text-gray-300 font-semibold w-[30%] text-sm">One-Time Password</label>
                <div class="flex-1 items-center">
                  <input
                    bind:value={otp}
                    id="otp"
                    name="otp"
                    type="text"
                    maxlength="6"
                    autocomplete="otp"
                    placeholder="000000"
                    autofocus
                    class="appearance-none block w-full px-3 py-2 text-sm rounded-md placeholder-gray-500 focus:outline-none select-text bg-verydark text-gray-300 border border-dark focus:ring-0 focus:border-gray-600"
                  />
                </div>
              </div>
            {/if}

            <div class="px-4 flex items-center text-xs justify-end">
              <label for="remember-me" class="inline-flex relative items-center cursor-pointer">
                <input type="checkbox" id="remember-me" class="sr-only peer" bind:checked={rememberMe} />
                <div
                  class="w-11 h-6 bg-verydark rounded-full peer peer-focus:ring-0 dark:bg-gray-700 peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-0.5 after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all dark:border-gray-600 peer-checked:bg-imblue"
                />
                <label for="remember-me" class="ml-2 block text-gray-300 cursor-pointer"
                  >Remember me on this computer</label
                >
              </label>
            </div>

            <div class="text-right">
              <!-- {#if loginLoading}
              <button
                disabled
                type="submit"
                class="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-imblue hover:bg-imblue focus:outline-none opacity-80"
              >
                Logging in..
              </button>
            {:else} -->

              {#if loginLoading == true}
                <div class="inline-block py-2 px-4">
                  <svg
                    class="animate-spin h-6 w-6 text-white"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                  >
                    <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4" />
                    <path
                      class="opacity-75"
                      fill="currentColor"
                      d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                    />
                  </svg>
                </div>
              {:else}
                <button
                  type="submit"
                  class="w-32 inline-flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-imblue hover:bg-imblue-dark focus:outline-none"
                >
                  Login
                </button>
              {/if}
            </div>
          </form>
        </div>
      </section>
    {/if}
  </div>
</div>
