import { get } from "svelte/store";

import { sessionToken, knownFiles, knownTasks, knownModules, systemInfo } from "../stores";

import { getFileChecksum } from "./files";
import { API_URL } from "./config";

const jwt = window.require("jsonwebtoken");
const fs = window.require("fs");
const zlib = window.require("zlib");

const JWT_API_EXPIRY = "5s";

const appVersion = window.require("@electron/remote").app.getVersion();

export const login = async ({ username, password, otp = "", system_uuid, remember = false }) => {
  const response = await fetch(`${API_URL}/api/v1/login`, {
    method: "post",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      username,
      password,
      otp,
      system_uuid,
      remember: String(remember),
      system_info: get(systemInfo),
      version: appVersion,
    }),
  });
  const result = await response.json();
  return result;
};

export const tokenLogin = async ({ token, system_uuid }) => {
  const response = await fetch(`${API_URL}/api/v1/login`, {
    method: "post",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({ token, system_uuid, system_info: get(systemInfo), version: appVersion }),
  });

  const result = await response.json();
  return result;
};

export const getUserBySessionToken = async ({ token }) => {
  const response = await fetch(`${API_URL}/api/v1/user?token=${token}`);
  const result = await response.json();
  return result.user;
};

export const getServerInfo = async () => {
  const response = await fetch(`${API_URL}/api/v1/config`);
  const result = await response.json();
  return result;
};

export const requestGameStart = async ({ files, tasks, gta_path }) => {
  try {
    const startRes = await fetch(`${API_URL}/api/v1/game/start?token=${get(sessionToken)}`);
    const { token_player_name, game_start_token } = await startRes.json();

    if (!game_start_token) return { result: { success: false } };

    const data = jwt.sign({ files, tasks, gta_path }, game_start_token, { expiresIn: JWT_API_EXPIRY });
    const startReportRes = await fetch(`${API_URL}/api/v1/game/start`, {
      method: "put",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ token: get(sessionToken), data }),
    });

    const startReportRet = await startReportRes.json();
    return {
      success: true,
      token_player_name,
      game_start_token,
      result: startReportRet,
    };
  } catch (error) {
    console.error(error);
    return {
      success: false,
    };
  }
};

export const submitRequestedFiles = async ({ crc = [], fullname }) => {
  const localFileMatches = get(knownFiles).filter((f) => crc.includes(f.crc));
  const localTaskMatches = get(knownTasks).filter((t) => crc.includes(t.crc));

  let exactMatches = [];

  if (fullname) {
    if (fs.existsSync(fullname)) exactMatches.push({ path: fullname });
  }

  const localMatches = [...localFileMatches, ...localTaskMatches, ...exactMatches];
  if (!localMatches.length) return;

  try {
    const localFiles = localMatches.map((lm) => {
      return {
        path: lm.path,
        crc: getFileChecksum(lm.path),
        contents: zlib.gzipSync(fs.readFileSync(lm.path)),
      };
    });

    const game_start_token = await getStartToken();
    const data = jwt.sign({ files: localFiles }, game_start_token, { expiresIn: JWT_API_EXPIRY });

    const gameSubmitRes = await fetch(`${API_URL}/api/v1/game/requests`, {
      method: "put",
      headers: {
        "Content-Type": "application/json",
      },
      body: new Blob([JSON.stringify({ token: get(sessionToken), data })], { type: "text/plain" }),
    });
  } catch (error) {
    console.error(error);
  }
};

export const submitGameData = async ({ modules = [], tasks = [] }) => {
  const game_start_token = await getStartToken();
  const data = jwt.sign({ modules, tasks }, game_start_token, { expiresIn: JWT_API_EXPIRY });

  const gameSubmitRes = await fetch(`${API_URL}/api/v1/game/data`, {
    method: "put",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({ token: get(sessionToken), data }),
  });

  const gameSubmitRet = await gameSubmitRes.json();
  return { success: true, result: gameSubmitRet };
};

export const submitGameCapture = async ({ rawData }) => {
  const game_start_token = await getStartToken();
  const data = jwt.sign({ data: rawData }, game_start_token, { expiresIn: JWT_API_EXPIRY });

  const gameSubmitRes = await fetch(`${API_URL}/api/v1/game/capture`, {
    method: "put",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({ token: get(sessionToken), data }),
  });

  const gameSubmitRet = await gameSubmitRes.json();
  return { success: true, result: gameSubmitRet };
};

const getStartToken = async () => {
  const startRes = await fetch(`${API_URL}/api/v1/game/start?token=${get(sessionToken)}`);
  const { game_start_token } = await startRes.json();
  return game_start_token;
};

export const encodeData = async (data) => {
  const game_start_token = await getStartToken();
  return jwt.sign(data, game_start_token, { expiresIn: JWT_API_EXPIRY });
};

export const getSavedTokens = () => {
  let savedTokens = [];
  try {
    savedTokens = JSON.parse(localStorage.getItem("savedTokens"));
  } catch (error) {
    // no saved tokens
    savedTokens = [];
  }

  if (!Array.isArray(savedTokens)) savedTokens = [];
  return savedTokens;
};
