<script>
  const shell = require("electron").shell;
  const { dialog } = require("@electron/remote");

  import { onMount } from "svelte";
  import { scanDirectory } from "./lib/files";
  import { gameState, knownFiles, knownModules, knownTasks, sampSettings, serverInfo } from "./stores";

  let localMods = [];

  onMount(async () => {
    $knownFiles = await scanDirectory({ dir: $sampSettings.gta_path });
    localMods = $knownFiles.filter((f) => f.name.substr(-3) == ".cs" || f.name.substr(-4) == ".asi");
  });
</script>

<div class="shadow overflow-hidden border-b border-gray-200 bg-white rounded-md max-h-full text-gray-900 p-6">
