      <p class="max-w-2xl text-sm text-gray-500">Recommended mods</p>
    </div>
    <div class="mt-6">
      <dl class="divide-y divide-gray-200">
        {#each localMods as localMod}
          <div class="py-4 items-center flex space-x-4">
            <div class="flex-1">
              <div class="text-base font-medium text-gray-900 truncate">{localMod.name}</div>
              <p class="text-sm text-gray-500 truncate" />
            </div>
            <div class="justify-self-end">
              <button
                class="bg-gray-100 text-gray-900 border border-gray-300 px-3 py-2 rounded-md text-sm font-semibold shadow-sm flex items-center"
              >
                <svg
                  class="w-4 h-4 mr-2"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                  ><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M5 13l4 4L19 7" /></svg
                >

                Installed</button
              >
            </div>
          </div>
        {/each}
        <div class="py-4 items-center flex space-x-4">
          <div class="flex-1">
            <div class="text-base font-medium text-gray-900 truncate">SilentPatchSA.asi</div>
            <p class="text-sm text-gray-500 truncate">Fixes various bugs in GTA:SA and SA:MP</p>
          </div>
          <div class="justify-self-end">
            <button
              class="bg-imblue text-white px-3 py-2 rounded-md hover:bg-imblue-light cursor-pointer text-sm font-semibold shadow-sm"
              >Install</button
            >
          </div>
