const fs = window.require("fs");
const rra = window.require("recursive-readdir-async");
const crcTools = window.require("crc");

const includedExtensions = [
  ".exe",
  ".cs",
  ".lua",
  ".cleo",
  ".ifp",
  ".asi",
  ".dll",
  ".sf",
  ".ahk",
  ".saa",
  ".saa2",
  "pedstats.dat",
];

export const scanDirectory = async ({ dir, stats = true, crc = true }) => {
  let gtaDirContents = await rra.list(dir, { extensions: true, stats });

  return gtaDirContents
    .filter((f) => includedExtensions.indexOf(f.extension) !== -1)
    .map((f) => {
      return {
        name: f.name,
        path: f.fullname,
        crc: crc ? crcTools.crc32(fs.readFileSync(f.fullname)).toString(16) : null,
        size: +(f.stats && f.stats.size / 1024),
      };
    });
};

export const getFileChecksum = (path) => {
  try {
    return crcTools.crc32(fs.readFileSync(path)).toString(16);
  } catch (error) {
    return null;
  }
};
