<script>
  const { ipcRenderer } = require("electron");
  const { dialog } = require("@electron/remote");

  let updating = false;
  let ready = false;

  import { getSampSettings } from "./lib/samp";
  import { createSocketConnection, destroySocketConnection } from "./lib/socket";
  import { getAutoHotkeyScripts, getSystemInfo } from "./lib/sysinfo";
  import { browseGameFolder, getGameState, readModules } from "./lib/game";
  import log from "./lib/logger";

  import {
    sessionToken,
    sampSettings,
    systemInfo,
    playerInfo,
    knownTasks,
    knownFiles,
    knownModules,
    onlinePlayers,
    gameState,
    serverInfo,
  } from "./stores";

  import { tokenLogin, getUserBySessionToken, getServerInfo, getSavedTokens } from "./lib/auth";
  import { onDestroy, onMount } from "svelte";

  import Login from "./Login.svelte";
  import LauncherNavigation from "./LauncherNavigation.svelte";
  import Tailwind from "./Tailwind.svelte";

  let gameWasRunning = false;
  let dlProgress = 0;

  ipcRenderer.on("updateDownloadProgress", function (event, data) {
    dlProgress = parseFloat(data).toFixed(1);
  });

  onMount(async () => {
    log("onmount", `user agent = ${window.navigator.userAgent}`);

    if (window.navigator.userAgent.includes("imrpupdater/")) {
      updating = true;
      return;
    }

    log("onmount", `getting system info..`);
    if (!$systemInfo) $systemInfo = await getSystemInfo();

    log("onmount", `getting samp settings..`);
    $sampSettings = await getSampSettings();
    log("onmount", `gta_path => ${$sampSettings.gta_path}`);

    log("onmount", `getting server config..`);
    $serverInfo = await getServerInfo();

    log("onmount", `trying to restore session..`);
    await restoreSessionToken();

    setInterval(async () => {
      $gameState = await getGameState();

      if (gameWasRunning === true && $gameState.running === false) {
        window.close();
      }

      if ($gameState.running === true) {
        gameWasRunning = true;
      }
    }, 2000); // get game state every 2 seconds

    if (!$sessionToken) {
      ready = true;
    } else {
      log("onmount", `create socket connection..`);
      createSocketConnection();
      log("onmount", `trying to restore existing session..`);
      await refreshSessionInfo();
    }
  });

  const restoreSessionToken = async () => {
    let hasAuthToken = localStorage.getItem("authToken");
    if (hasAuthToken) {
      const autoLoginResult = await tokenLogin({
        token: hasAuthToken,
        system_uuid: $systemInfo.system.uuid,
      });

      if (autoLoginResult.success) {
        $sessionToken = autoLoginResult.sessionToken;
      } else {
        // localStorage.removeItem("authToken");
        alert("Automatic login has failed, please login again.");
      }
    }
  };

  const refreshSessionInfo = async () => {
    if ($sessionToken) {
      $playerInfo = await getUserBySessionToken({ token: $sessionToken });
      ready = true;
    }
  };

  const unsubscribeTokenListener = sessionToken.subscribe(async (newToken) => {
    await refreshSessionInfo();
  });

  onDestroy(unsubscribeTokenListener);
</script>

<Tailwind />

{#if ready === false}
  <div class="launcher-wrapper h-screen select-none flex items-center justify-center text-white bg-verydark">
    {#if updating === true}
      <svg
        class="animate-spin mr-3 h-8 w-8 text-white"
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        viewBox="0 0 24 24"
      >
        <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4" />
        <path
          class="opacity-75"
          fill="currentColor"
          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
        />
      </svg>

      {#if dlProgress > 99}
        <div class="text-lg">Download complete, extracting and installing..</div>
      {:else}
        <div class="text-lg">Downloading an update.. you're already {dlProgress}% done</div>
      {/if}
    {:else}
      <svg
        class="animate-spin mr-3 h-8 w-8 text-white"
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        viewBox="0 0 24 24"
      >
        <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4" />
        <path
          class="opacity-75"
          fill="currentColor"
          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
        />
      </svg>
    {/if}
  </div>
{:else if ready === true}
  {#if !$sessionToken}
    <Login />
  {:else}
    <div
      class="launcher-wrapper h-screen max-h-screen overflow-hidden select-none flex flex-col bg-verydark text-white"
    >
      <!-- <div class="top-bar inline-flex items-center w-full p-8">
        <div class="flex-none">
          <img src="./images/logo-white.png" alt="Logo" class="w-72 h-auto" />
        </div>
        <div class="flex-1 font-semibold text-right text-white justify-end">
          <span>{$playerInfo.name.replace("_", " ")}</span>
          <br />
          <span class="text-xs font-semibold text-white opacity-50 hover:opacity-70 cursor-pointer" on:click={doLogout}
            >Switch Character / Logout</span
          >
        </div>
      </div> -->

      <LauncherNavigation />
    </div>
  {/if}
{/if}
