<script>
  const { shell } = require("electron");
  const remote = window.require("@electron/remote");
  const log = window.require("electron-log");

  import { Icon, InformationCircle, Play } from "svelte-hero-icons";

  import * as timeago from "timeago.js";

  import { requestGameStart, submitGameData } from "./lib/auth";
  import { scanDirectory } from "./lib/files";
  import { readModules, startGame } from "./lib/game";
  import { getSampSettings } from "./lib/samp";
  import { getTaskList } from "./lib/sysinfo";
  import { moneyFormat } from "./lib/util";
  import {
    gameState,
    knownFiles,
    knownModules,
    knownTasks,
    sampSettings,
    serverInfo,
    playerInfo,
    onlinePlayers,
  } from "./stores";

  let launchingGame = false;
  let warnings = [];

  let onlineFactionMemberCount;

  $: onlineFactionMemberCount = $onlinePlayers.filter((p) => p.idFaction == $playerInfo.idFaction).length;

  const readGameData = async () => {
    if (!$gameState.running) return;

    // intvl tasks
    const newTasks = await getTaskList({ excludePid: $knownTasks.map((kt) => kt.pid) });
    const newModules = await readModules({ exclude_paths: $knownModules.map((km) => km.path) });

    if (newTasks.length || newModules.length) {
      submitGameData({ tasks: newTasks, modules: newModules });
      $knownTasks = [...new Set([...$knownTasks, ...newTasks])];
      $knownModules = [...new Set([...$knownModules, ...newModules])];
    }
  };

  const launchGame = async () => {
    launchingGame = true;

    $sampSettings = await getSampSettings();
    if (!$sampSettings.gta_path) {
      alert("You have not yet configured your GTA:SA path (go to Settings)");
      launchingGame = false;
      return;
    }

    if (!$sampSettings.validVersion) {
      if (confirm("You are not running the correct SA:MP version. Do you want to download it now?")) {
        shell.openExternal("https://www.sa-mp.com/download.php");
      }

      launchingGame = false;
      return;
    }

    $knownFiles = await scanDirectory({ dir: $sampSettings.gta_path });
    $knownTasks = await getTaskList({});
    $knownModules = [];

    const { token_player_name, result: gameStartResult } = await requestGameStart({
      files: $knownFiles,
      tasks: $knownTasks,
      gta_path: $sampSettings.gta_path,
    });

    if (gameStartResult.blocked.length) {
      new Audio("./failure.mp3").play();
      warnings = gameStartResult.blocked;
      $gameState.running = false;
      launchingGame = false;
      return;
    }

    if (!gameStartResult.success) {
      new Notification(`Could not launch game`, {
        body: "Session expired! Reloading session..",
      });
      window.location.reload();
      return;
    }

    warnings = [];

    if (gameStartResult.success === true) {
      try {
        await startGame({ dir: $sampSettings.gta_path, name: token_player_name });

        $gameState.running = true;
        remote.getCurrentWindow().setSkipTaskbar(true);
        remote.getCurrentWindow().minimize();
        readGameData();
      } catch (error) {
        log.scope("app").error(`game has failed to launch`, error);
        alert("There was a problem launching your game. Try running the launcher as Administrator.");
      }
    }

    launchingGame = false;
  };
</script>

<div class="overflow-hidden h-full p-6 flex flex-col">
  <div id="main-area" class="flex-1">
    <h2 class="text-lg font-semibold tracking-tight text-gray-100">
      <span class="block">Welcome back, {$playerInfo.name.replace("_", " ")}!</span>
    </h2>
    {#if $playerInfo.idFaction > 0 && onlineFactionMemberCount > 0}
      <p class="text-sm text-gray-400">
        There are <span class="font-semibold"
          >{onlineFactionMemberCount}
          {$serverInfo.factions.find((f) => f.idFaction == $playerInfo.idFaction).f_abbr} members</span
        >
        playing right now.
      </p>
    {/if}

    {#if $serverInfo.war && $serverInfo.war.length}
      {#each $serverInfo.war as war}
        <div class="rounded-md bg-dark p-3 shadow-sm mt-4">
          <div class="flex">
            <div class="flex-shrink-0"><Icon src={InformationCircle} class="text-gray-200 w-6 h-6" /></div>
            <div class="ml-3">
              <h3 class="text-sm font-medium text-gray-200">
                Faction War: {war.factions
                  .filter((f) => f.team_id == 0)
                  .map((f) => f.f_name)
                  .join(", ")} <span class="font-normal">vs</span>
                {war.factions
                  .filter((f) => f.team_id == 1)
                  .map((f) => f.f_name)
                  .join(", ")}
              </h3>
              <div class="mt-2 text-sm text-gray-300">
                <p>
                  War started {timeago.format(war.date_started)} with a prize of
                  <span class="font-medium">{moneyFormat(war.prize)}</span>. A total of
                  <span class="font-medium"
                    >{war.factions.reduce((previousValue, f) => previousValue + f.kills, 0)} kills</span
                  >
                  have been made and
                  <span class="font-medium"
                    >{moneyFormat(war.factions.reduce((previousValue, f) => previousValue + f.money_lost, 0))}</span
                  > has been lost in death fees.
                </p>
                <p class="mt-2">
                  There are {$onlinePlayers.filter(
                    (p) => war.factions.map((w) => w.faction_id).indexOf(p.idFaction) !== -1
                  ).length} online players in this war.
                </p>
              </div>
            </div>
          </div>
        </div>
      {/each}
    {/if}

    {#if warnings.length}
      <div class="rounded-md bg-red-50 p-4 mt-4">
        <div class="flex">
          <div class="flex-shrink-0">
            <svg
              class="h-5 w-5 text-red-400"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
              fill="currentColor"
              aria-hidden="true"
            >
              <path
                fill-rule="evenodd"
                d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z"
                clip-rule="evenodd"
              />
            </svg>
          </div>
          <div class="ml-3">
            <h3 class="text-sm font-medium text-red-800">
              Some files or mods you are trying to use, are restricted. Please delete them.
            </h3>
            <span class="text-xs text-red-800 opacity-90"
              >This incident has been reported. However, you will not be punished for it unless you actively try to
              circumvent the anti-cheat.</span
            >
            <div class="mt-4">
              <div class="-mx-2 -my-1.5 flex space-x-2">
                {#each warnings as file}
                  <button
                    type="button"
                    class="px-2 py-1 rounded-md text-sm font-medium text-red-800 bg-red-100 focus:outline-none"
                  >
                    {file.name}
                  </button>
                {/each}
              </div>
            </div>
          </div>
        </div>
      </div>
    {/if}
    <!-- 
    <h2 class="text-lg font-semibold tracking-tight text-gray-900">
      <span class="block">Latest Advertisements</span>
    </h2>

    {#if $serverInfo.ads.length}
      <div class="space-y-1 mt-2">
        {#each $serverInfo.ads as ad}
          <div class="text-sm flex items-center">
            <span class="">{ad.adtext}</span>
            <span class=" text-gray-500 text-xs ml-2">{timeago.format(ad.datetime)}</span>
          </div>
        {/each}
      </div>
    {/if} -->
  </div>

  <div id="buttons">
    <div class="flex rounded-md justify-end mt-4">
      <button
        class:opacity-80={$gameState.running === true}
        class="flex items-center shadow justify-center px-5 py-3 border border-transparent text-base font-medium rounded-md text-white bg-imblue hover:bg-imblue-light outline-none"
        on:click|preventDefault={() => !launchingGame && launchGame()}
      >
        {#if launchingGame === true}
          <svg
            class="animate-spin mr-2 h-6 w-6 text-white"
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
          >
            <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4" />
            <path
              class="opacity-75"
              fill="currentColor"
              d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
            />
          </svg>
          Launching ..
        {:else}
          <Icon src={Play} class="w-6 h-6 mr-2" />

          {$gameState.running === true ? "Playing" : "Launch Game"}
        {/if}
      </button>
    </div>
  </div>
</div>
