<script>
  const shell = require("electron").shell;
  const { dialog } = require("@electron/remote");

  import { Icon, CheckCircle, ExclamationCircle, Collection, FolderOpen } from "svelte-hero-icons";

  import { browseGameFolder } from "./lib/game";
  import { getSampSettings } from "./lib/samp";
  import { sampSettings, systemInfo, useAsianServer } from "./stores";

  $: console.log($useAsianServer);

  const onPathSelected = async (path) => {
    path = path.filePaths[0];

    localStorage.setItem("gta_path", path);
    const newSampSettings = await getSampSettings();
    if (!newSampSettings.validVersion) {
      localStorage.removeItem("gta_path");

      dialog.showMessageBox({
        message: `The path you selected does not have the correct SA:MP version installed.\n\n${path}`,
        type: "error",
        title: "Invalid folder",
      });
    } else {
      $sampSettings = newSampSettings;
    }
  };
</script>

<div class="overflow-hidden h-full p-6 flex flex-col">
  <div id="main-area" class="flex-1">
    <h2 class="text-lg font-semibold tracking-tight text-gray-100 mb-4">
      <span class="block">General Settings</span>
    </h2>

    <!-- <p class="max-w-2xl text-sm text-gray-300">
        If you do not customize your GTA directory here, the default will be used.
      </p> -->
    <div class="space-y-2 text-sm">
      <div class="py-4 px-6 flex items-center bg-dark rounded-md">
        <div class="text-gray-200 flex-1 font-semibold">GTA: San Andreas Path</div>
        <div class="flex items-center">
          <div class="">
            <span class="text-gray-300">{$sampSettings.gta_path}</span>
            <div class="space-x-2 mt-2 flex items-center justify-end">
              {#if $sampSettings.gta_path}
                <button
                  type="button"
                  on:click|preventDefault={() => {
                    browseGameFolder({ dir: $sampSettings.gta_path });
                  }}
                  class="inline-flex items-center border border-gray-900 px-3 py-1.5 shadow-sm text-xs leading-4 font-medium rounded-md text-gray-300 bg-opacity-80 bg-verydark hover:bg-opacity-60 focus:outline-none"
                >
                  Browse</button
                >{/if}<button
                type="submit"
                webkitdirectory
                on:click|preventDefault={async () => {
                  const path = await dialog.showOpenDialog({
                    properties: ["openDirectory"],
                    defaultPath: $sampSettings.gta_path || "",
                    title: "Select your GTA: San Andreas installation directory",
                  });
                  if (path) onPathSelected(path);
                }}
                class="inline-flex items-center border border-gray-900 px-3 py-1.5 shadow-sm text-xs leading-4 font-medium rounded-md text-gray-300 bg-opacity-80 bg-verydark hover:bg-opacity-60 focus:outline-none"
              >
                <Icon src={FolderOpen} class="w-4 h-4 mr-2" />
                Select</button
              >
            </div>
          </div>
        </div>
      </div>

      <div class="py-4 px-6 flex items-center bg-dark rounded-md">
        <div class="text-gray-200 flex-1 font-semibold">SA:MP Installed</div>
        <div class="flex items-center">
          {#if $sampSettings.version}
            <span class="text-gray-300">{$sampSettings.version.name}</span>
            <Icon src={CheckCircle} class="text-green-600 w-6 h-6 ml-2" />
          {:else}
            <span class="text-gray-300">Unknown</span>
            <Icon src={ExclamationCircle} class="text-red-600 w-6 h-6 ml-2" />
          {/if}
        </div>
      </div>

      <div class="py-4 px-6 flex items-center bg-dark rounded-md">
        <div class="text-gray-200 flex-1 font-semibold">Operating System</div>
        <div class="flex items-center text-gray-300">
          <span
            >{$systemInfo.os.distro} ({$systemInfo.os.arch})
            <span class="text-gray-400">[Build #{$systemInfo.os.build}]</span></span
          >
        </div>
      </div>
    </div>

    <h2 class="text-lg font-semibold tracking-tight text-gray-100 mb-4 mt-8">
      <span class="block">Advanced</span>
    </h2>

    <div class="space-y-2 text-sm">
      <div
        class="py-4 px-6 flex items-center bg-dark rounded-md cursor-pointer"
        on:click|preventDefault={() => {
          $useAsianServer = !$useAsianServer;
        }}
      >
        <div class="flex-1">
          <div class="text-gray-200 font-semibold">Asia Server</div>
          <div class="text-gray-400 text-xs max-w-[80%]">
            May improve your connection to the server if you are playing from Asia. Currently only for testing purposes.
            Unstable.
          </div>
        </div>
        <div class="inline-flex items-center text-gray-300">
          <label for="checked-toggle" class="inline-flex relative items-center cursor-pointer">
            <input type="checkbox" id="checked-toggle" class="sr-only peer" bind:checked={$useAsianServer} />
            <div
              class="w-11 h-6 bg-gray-200 rounded-full peer peer-focus:ring-0 dark:bg-gray-700 peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-0.5 after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all dark:border-gray-600 peer-checked:bg-imblue"
            />
          </label>
        </div>
      </div>
    </div>
  </div>
</div>
